$icons: (0:0)
$icons: map-merge($icons,(bc: (X: -23px, Y:-85px, W: 16px, H: 16px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-call: (X: -47px, Y:-62px, W: 19px, H: 19px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-cart: (X: -31px, Y:0px, W: 27px, H: 27px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-clock: (X: -24px, Y:-62px, W: 19px, H: 19px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-fb: (X: -62px, Y:0px, W: 23px, H: 24px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-heart: (X: -89px, Y:-64px, W: 16px, H: 16px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-like: (X: 0px, Y:-62px, W: 20px, H: 19px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-mail: (X: 0px, Y:-31px, W: 27px, H: 27px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-message: (X: 0px, Y:-85px, W: 19px, H: 16px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-search: (X: -89px, Y:0px, W: 18px, H: 18px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-share-grey: (X: -89px, Y:-43px, W: 17px, H: 17px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-share: (X: -89px, Y:-22px, W: 17px, H: 17px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-star: (X: -70px, Y:-62px, W: 13px, H: 12px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-twitter: (X: -62px, Y:-28px, W: 23px, H: 24px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-vk-small: (X: -31px, Y:-31px, W: 24px, H: 24px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(icon-vk: (X: 0px, Y:0px, W: 27px, H: 27px, TW: 107px, TH: 101px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-position($iconName)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW) 	
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2  	


@mixin s($i)
	+sprite($i)
@mixin sp($i)
	+sprite-position($i)
@mixin sr($i)
	+sprite-retina($i)	

